@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0px;
  font-family: "Quicksand";
  color: #333;
}
.text-center {
  width: 80%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}
.postactions {
  display: flex;
  justify-content: space-evenly;
  max-width: 250px;
  margin-left: auto;
  margin-right: auto;
}
.card-body label {
  text-align: left;
  display: block;
}
.card-body input,
.card-body textarea,
.card-body select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.editPost {
  display: none;
}

.navbar-brand {
  margin: 0px;
}
.addpost {
  padding-top: 20px;
}
.creatPostForm {
  display: none;
}
.signin,
.signup {
  padding-top: 20px;
}
.signincard {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.card-header {
  font-size: 20px;
}
.card-body p {
  padding-top: 20px;
  font-size: 14px;
}
.bg-danger {
  padding: 5px;
}
